import React from 'react';
import {useTypewriter, Cursor} from 'react-simple-typewriter';
import icon from "./images/startup.svg";


import {Container, Row, Col} from 'react-bootstrap';
import './banner.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {  } from '@fortawesome/free-brands-svg-icons';
import {Link, Button} from 'react-scroll';


function Banner(){
const [text]= useTypewriter({
    words: ['Front End Developper','Backend Developper','FullStack Developer'],
    loop:3,
    typeSpeed: 120,
    deleteSpeed: 50

});
    return (
        <section className='banner ' id="home">
        <Container >
            
        <Row className=" text-align-center  ">
            <Col className=" col-md-6 col-md-offset-3 mt-5 text-white " lg={7} >
            <div className="part1 ">
                <p className='but'>You find every thing in my portofolio</p>
                <h2 className='text-align-center text'>
                <p className='intro'>I'M Madiha Kharoubi, {''}</p>
                <p>Computer Science Eng,</p>
                <p>Living in Algeria,<span id="cl"> {text}</span><span><Cursor cursorStyle="<" /></span> </p>

               
                
            </h2>
            <p className='ptext'>Hello, 11 years of experience in management, junior in front End Developping</p>
            <Link
            activeClass="active"
            spy={true}
            smooth={true}
            offset={-70}
            duration={1000}
            targetId="projects"
           to="projects"
            
                    
            
            activeClassName="selected">
            <button className='connect btn '>Let's Connect <span><i class="fa-regular fa-circle-right"></i></span></button></Link>
            </div>
            </Col>
            <Col className="columns mt-5">
                <div className='part1 '>
            <img  className="me"  src={icon} alt='spider' />
            
            </div>
            </Col>
            </Row>
           

        </Container>
        </section>
    )

} export default Banner;